import React, { Component } from 'react';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import RemoveCircleIcon from '@mui/icons-material/RemoveCircle';
import Divider from '@mui/material/Divider';


class Country extends Component {

    state = {
        name: 'United States',
        gold: 0,
      }

      handleClick = () => {
        this.setState(prev => ({ gold: prev.gold + 1 }));
      }

      handleClickMinus = () => {
        if (this.state.gold >= 1) {
          this.setState(prev => ({ gold: prev.gold - 1 }));
        }
      }

  render() { 
    return (
        <div style={{marginLeft: 'auto', marginRight: 'auto', marginTop: '5rem', alignItems: 'center', width: '25%', borderStyle: 'outset', borderColor: 'yellow', borderWidth: '1rem'}}> 
            <div className='Name' style={{justifyContent: 'center', alignItems: 'center'}}>
                <h1>{ this.state.name }</h1><Divider style={{ background: 'yellow', height: '.5em' }} variant="middle" />
            </div>
            <div className='count' style={{display: 'flex', alignItems: 'center', justifyContent: 'center', marginTop: '20px'}}>
                <RemoveCircleIcon fontSize="large" style={{color: "yellow", marginRight: '10px'}} className='Count' onClick={ this.handleClickMinus }>+</RemoveCircleIcon>
                <AddCircleIcon fontSize="large" style={{color: "yellow", marginRight: '10px'}} className='Count' onClick={ this.handleClick }>+</AddCircleIcon>
                <span><h2>Medals: { this.state.gold }</h2></span>
            </div> 
        </div>
      );
  }
}

export default Country